import {Component, ComponentInterface, Event, EventEmitter, h, Host, Method, Prop, Watch} from '@stencil/core';
import {OptionHelper} from '../../services/OptionHelper';
import {throttle} from '../../utils/Throttle';
import {SelectHelper} from '../../services/SelectHelper';

@Component({
  tag: 'soul-avatar-option',
  styleUrl: 'soul-avatar-option.scss',
  shadow: true
})
export class SoulAvatarOption implements ComponentInterface {

  @Prop({reflect: true}) public disabled = false;
  @Prop({reflect: true}) public hidden = false;
  @Prop({reflect: true}) public highlighted = false;
  @Prop({reflect: true}) public passive = false;
  @Prop({reflect: true}) public selected = false;
  @Prop({reflect: true}) public dataSelected = false;
  @Prop({reflect: true}) public nonFilterable = false;
  @Prop({reflect: false, mutable: false}) public readonly instanceId = `js-soul-avatar-option-${++avatarOptionId}`;
  @Prop() public value: any;

  @Event() public soulOptionChange: EventEmitter<SoulAvatarOption>;
  @Event() public soulOptionClick: EventEmitter<SoulAvatarOption>;
  @Event() public soulOptionHover: EventEmitter<SoulAvatarOption>;
  @Event() public soulOptionSelectChange: EventEmitter<SoulAvatarOption>;

  private avatarSlot: HTMLSlotElement;
  private infoSlot: HTMLSlotElement;
  private optionHelper: OptionHelper;
  private hostElement: HTMLElement;
  private observer: MutationObserver;

  constructor() {
    this.observer = new MutationObserver((mutations) => {
      for (const mutation of mutations) {
        if (mutation.type === 'attributes' && mutation.attributeName === SelectHelper.GROUP_DISABLED_ATTRIBUTE) {
          this.onDisabledChange();
        }
      }
    });
  }

  public componentDidLoad() {
    this.optionHelper = new OptionHelper(this.onChange.bind(this), [this.infoSlot, this.avatarSlot]);
    if (this.selected) {
      this.onSelectedChange();
    }
    this.onDisabledChange();
  }

  public componentDidRender() {
    this.observer.disconnect();
    this.observer.observe(this.hostElement, {attributes: true});
  }

  public disconnectedCallback() {
    this.optionHelper?.disconnect();
    this.observer.disconnect();
  }

  @Method()
  public clone(): Promise<HTMLSoulAvatarOptionElement> {
    const clonedElement = document.createElement('soul-avatar-option');
    clonedElement.passive = true;
    const clonedSlotContents: Element[] = this.optionHelper.cloneSlotsContent();
    clonedElement.append(...clonedSlotContents);
    return Promise.resolve(clonedElement);
  }

  @Method()
  public async match(term: string): Promise<boolean> {
    return this.avatarSlot.assignedElements().some((avatar) => {
      return (avatar as HTMLSoulAvatarElement).name?.toLowerCase().includes(term.toLowerCase()) ||
        (avatar as HTMLSoulAvatarElement).additionalText?.toLowerCase().includes(term.toLowerCase());
    });
  }

  @Watch('disabled')
  public onDisabledChange(): void {
    if (this.optionHelper) {
      this.avatarSlot.assignedElements().forEach((avatar) => {
        (avatar as HTMLSoulAvatarElement).muted = this.disabled || this.hostElement.hasAttribute(SelectHelper.GROUP_DISABLED_ATTRIBUTE);
      });
    }
  }

  @Watch('selected')
  public onSelectedChange(): void {
    if (this.optionHelper && !this.passive) {
      this.dataSelected = this.selected;
      this.optionHelper.onSelectChange(this.selected);
      this.soulOptionSelectChange.emit(this);
    }
  }

  render() {
    return (
      <Host ref={el => this.hostElement = el as HTMLElement}>
        <div class="soul-avatar-option" onMouseMove={throttle(this.onMouseMove.bind(this), 1000)} onMouseDown={(event) => this.onMouseDown(event)}>
          <div class="soul-grid soul-grid--horizontal soul-grid--align-center soul-grid--compact-gap">
            <div class="soul-grid-item-fill-span">
              <slot ref={el => this.avatarSlot = el as HTMLSlotElement} name="avatar"></slot>
            </div>
            <div class="soul-grid-item-auto-span">
              <slot ref={el => this.infoSlot = el as HTMLSlotElement} name="info"></slot>
            </div>
          </div>
        </div>
      </Host>
    );
  }

  private onChange(): void {
    if (this.dataSelected && !this.passive) {
      this.soulOptionChange.emit(this);
    }
  }

  private onMouseMove(): void {
    if (!this.passive && !this.isOptionDisabled()) {
      this.soulOptionHover.emit(this);
    }
  }

  private onMouseDown(event: MouseEvent): void {
    if (!this.passive) {
      event.preventDefault();
      event.stopPropagation();
      if (!this.isOptionDisabled()) {
        this.soulOptionClick.emit(this);
      }
    }
  }

  private isOptionDisabled(): boolean {
    return this.disabled || this.hostElement.hasAttribute(SelectHelper.GROUP_DISABLED_ATTRIBUTE);
  }
}

let avatarOptionId = 0;
